import create from './index.tpl'
import './index.styl'

const component = create({
  name: 'Textareaer',

  props: {
    // 
    value: {
    },

    // 
    max: {
      type: [Number, String]
    },

    // 
    rows: {
      type: [Number, String],
      default: 3
    },

    // 
    cols: {
      type: [Number, String],
      default: 30
    },

    // 
    height: [Number, String],

    // 
    readonly: Boolean,

    // 
    label: String,

    // 
    name: String,

    // 
    placeholder: {
      type: String,
      default: ''
    },

    // 
    showCounter: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      val: this.value
    }
  },

  watch: {
    // 
    val(newVal) {
      this.$emit('input', newVal)
    },

    value(newVal) {
      this.val = newVal
    }
  },

  computed: {
    textareaStyles() {
      if (this.height) {
        return {
          height: `${this.height}px`
        }
      }
    }
  }
})

export default {
  install(Vue) {
    Vue.component(component.name, component)
  }
}



